.question-wrapper {
  display: flex;
  margin: 20px 0;
  max-height: 100px;
  overflow: auto;

  .question-no {
    font-weight: bold;
    font-size: 16px;
  }

  .question-text {
    font-size: 14px;
    margin-left: 20px;
  }
}

#waveform {
  margin-top: 10px;
  border-width: 2px;
  background-color: #daf1f2;
  height: 150px;
  width: 100%;

  wave {
    height: 100% !important;
    background-color: #f1f3f6 !important;
    // width: 100% !important;
  }
}

.custom-event {
  font-size: 10px;
  font-weight: bold;
  position: absolute;
  color: #fff;
  background-color: #333;
  padding: 5px 10px;
  border-radius: 4px;
  z-index: 100;
}
.VideoAnalyser {
  .video-js {
    width: 49%;
    height: 300px;
  }
}
.new-video .vjs-control-bar {
  display: none !important;
}

.timeline {
  display: flex;
  width: 1160px;
  position: relative;
  bottom: 75px;
  z-index: 100;
  height: 1px;
  background-color: #c69e5d;

  .flex-flex {
    position: absolute;
    margin-top: -30px;
  }

  .flex-div {
    // text-align: center;
    font-size: 10px;
    font-weight: bold;
    position: relative;
    .Icon-VideoAnayzer {
      color: #c69e5d;
      border: 1px solid #c69e5d;
      padding: 5px;
      border-radius: 50%;
      background: #fff;
      width: 30px;
      height: 30px;
      position: absolute;
      left: -15px;
    }
  }
}
.recording-modal {
  height: 90% !important;
  top: 5% !important;
}
