.main-dummy{
    display: flex;
    justify-content: space-between;
    background-color: #fff;

    .left-dummy{
        width: 15%;
        padding: 80px 20px;
        background-color: #04724d;
        color: #fff;
        height: auto;

        .left-nav-heading{
            padding: 20px 0;
            cursor: pointer;
            font-weight: 600;
        }
    }

    .right-dummy{
        width: 70%;
        margin-left: 20px;
        .comment-dummy{
            padding: 10px 0;
            font-size: 13px;
        }
        .all-components-heading{
            font-size: 24px;
            font-weight: bold;
        }
        .component-heading{
            font-weight: bold;
            padding: 10px 0;
        }
        

        .all-components-div{
            margin: 50px 0px;

            .component-select,.component-input,.component-autocomplete{
                width: 40%;
                margin-bottom: 40px;
            }

            .component-stepper{
              width: 100%;
            }
         
            .component-button,.component-datepicker, .component-timepicker ,.component-datecard,.component-modal,.component-inputsearch{
                margin-bottom: 40px;
            }
          
            .jdCalenderDummy{
            display: flex;
            width: 300px;
            justify-content: space-between;
            }

            .searchInputWrap-dummy {
                display: flex;
                margin-left: 15px;
                width: 275px;
              }
              
              .searchInputWrap-dummy .MuiFormControl-root {
                background: #e1e5eb;
                border-top-left-radius: 4px;
                border-bottom-left-radius: 4px;
              }

              .jdSearch-dummy .MuiInputBase-root {
                border-top-left-radius: 4px;
                border-bottom-left-radius: 4px;
                font-size: 16px;
                padding: 5px 10px;
                border: none;
                background-color: #e1e5eb;
                background: transparent padding-box;
                width: 100%;
                /* height: 37px; */
                text-align: left;
              }
              
              .jdSearch-dummy .MuiOutlinedInput-root {
                border-top-right-radius: 0;
                border-bottom-right-radius: 0;
              }
              .searchiconJD-dummy {
                background-color: #e1e5eb;
                padding: 5px;
                border-top-right-radius: 4px;
                border-bottom-right-radius: 4px;
                cursor: pointer;
                display: flex;
                justify-content: center;
                align-items: center;
              }
              
              .searchiconJD-dummy img {
                height: 15px;
                width: 15px;
                margin: 0 5px;
              }
         

        }

        .date-new-dummy .css-1s2u09g-control,.date-new-2-dummy .css-1s2u09g-control {
            border-radius: 4px;
            height: 34px;
          }
    }
}

